.nav {
  $root: nav;
  position: fixed;
  left: 40px;
  top: 50%;
  z-index: $zindex-fab;
  transform: translateY(-50%);

  &.is-open {
    z-index: 999;
  }

  &__box {
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    padding: 40px 0px;
    width: 100%;
    min-width: 72px;
    max-width: 72px;
    border: 1px solid color(grey, 300);
    box-shadow: 0px 2px 15px rgba(0, 0, 0, 0.08);
    border-radius: 36px;
    text-align: center;
    background-color: $white;
    transition: all 0.6s cubic-bezier(0.33, 1, 0.68, 1);
    overflow: hidden;
    @include nav__for--aria-expanded($root);
  }

  &__list {
    display: flex;
    align-items: center;
    margin: 21.5px 0;

    &:only-child {
      margin: 0;
    }

    &:first-child {
      margin-top: 0;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__item {
    display: flex;
    align-items: center;
    position: relative;
    max-width: 72px;
    transition: all 0.6s cubic-bezier(0.33, 1, 0.68, 1);
  }

  .text {
    white-space: nowrap;
    margin-left: 8px;
    visibility: hidden;
    opacity: 0;
    transition: all 0.55s cubic-bezier(0.33, 1, 0.68, 1);
    transition-delay: 0.1s;
  }
}
