table {
  padding: 0;
  border: 0;
  border-spacing: 0px;
  border-collapse: collapse;
}

th,
td {
  padding: 0;
}

.viewer {
  &:after {
    content: '';
    height: 40px;
    display: block;
  }
}

.table {
  $root: table;

  width: 100%;
  position: relative;
  text-align: center;

  &-row {
    table-layout: fixed;
  }

  tr {
    th,
    td {
      font-size: 14px;
      font-weight: 500;
      height: 46px;
      padding: 0 16px;
      color: color(grey, 500);
      border: 1px solid color(grey, 200);
    }
  }

  thead {
    th {
      background-color: $white;
      color: $black;
    }
  }
}

.left {
  text-align: left !important;
}

.center {
  text-align: left !important;
}

.right {
  text-align: left !important;
}
