.accordion {
  $root: accordion;
  + .pagination {
    margin-top: 30px;
  }
  &__item {
    border-top: 1px solid color(grey, 200);
    &:first-child {
      border-top: 0;
    }

    &:last-child {
      border-bottom: 1px solid color(grey, 200);
    }

    &.is-focused {
      .list--type3 .list__row .list__item:nth-child(2) {
        font-weight: 700;
        color: color(grey, 900);
      }
    }
  }

  &__box {
    border-top: 1px solid color(grey, 900);
  }

  &__fixed {
    padding: 20px;
  }

  &__inner {
    padding: 30px 87px 40px 20px;
    background-color: color(grey, 200);
  }
  &__head {
    position: relative;
  }

  &__check {
    display: inline-block;
    vertical-align: top;
  }

  &__button {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    &.is-active {
      &:after {
        transform: rotate(-180deg);
        transition: transform 0.6s cubic-bezier(0.22, 0.86, 0.62, 1.06);
      }
    }

    @include list--type3__aria;
  }

  &--type2 {
    .#{$root}__item {
      transition: all 0.4s ease-out;
      border: 1px solid color(grey, 300);
      border-radius: 7px;
      margin-top: 10px;

      &:first-child {
        margin-top: 0;
      }

      .ic-button-refresh {
        display: none;
      }

      &.is-focused {
        border: 1px solid color(blue, 800) !important;
      }

      @include card__for--interation($root);
    }
    .#{$root}__fixed {
      min-height: 64px;
    }

    .#{$root}__panel {
      overflow: hidden;
      border-bottom-left-radius: 7px;
      border-bottom-right-radius: 7px;
    }
    .#{$root}__head__inner {
      display: flex;
      align-items: center;
      position: relative;
      background-color: color(grey, 50);
      padding: 20px;
      border-top-left-radius: 7px;
      border-top-right-radius: 7px;
    }

    .#{$root}__button {
      width: calc(100% - 48px);

      &:after {
        display: none;
      }
    }
    .#{$root}__box {
      border-top: 0;
    }
    .#{$root}__inner {
      padding: 0 20px 20px;
      background-color: $white;
    }

    .expresstion-list {
      .ic-expresstion-arrow--type2 {
        pointer-events: none;
        margin: 0 8px;
      }

      .dropdown {
        min-width: 148px;
        width: 50%;

        label {
          min-width: 120px;
          width: calc(100% - 28px);
        }
      }

      > span {
        // width: 120px;
        min-width: 120px;
        width: 50%;
        font-weight: 500;
        font-size: 16px;
        line-height: 24px;
      }

      margin-left: 8px;
      &:before {
        display: none;
      }

      &:last-child {
        padding: 0;
      }
    }

    .list {
      &:first-child {
        margin-top: 0;
      }
      margin-top: 20px;
    }
  }
}
